.todolist {
    height: 70vh;
    width: 50%;

    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (max-width: 700px) {
    .todolist {
        width: 90%;
    }
}

@media (max-width: 1200px) {
    .todolist {
        width: 90%;
    }
}